import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Route } from "react-router-dom";
import axios from 'axios';

import AnimatedSwitch from '../components/animated-switch';

import VisitorForm from '../components/VisitorForm';
import DataTable from '../components/DataTable';
import getCookie from '../utils/getCookie';

export default class VisitorsPages extends Component {
    render() {
        let match = this.props.match;
        return <AnimatedSwitch>
            <Route path={`${match.path}/new`} render={(props) => <VisitorNew {...props} /> } />
            <Route path={`${match.path}/:visitorId`} render={(props) => <VisitorEdit {...props} /> } />
            <Route exact path={`${match.path}`} render={(props) => <Visitors {...props} /> } />
        </AnimatedSwitch>
    }
}

export class Visitors extends Component {
    constructor(props) {
        super(props);
        window.visitorListActionEdit = this.edit;
        this.dataTable = React.createRef();
        this.dataTableConfig = {
            order: [[ 5, "desc" ]],
            serverSide: true,
            processing: true,
            ajax: function(params, callback, settings) {
                axios.get('/api/visitors', {params: {params}}).then(response=>{
                    callback(response.data);
                })
            },
            columns: [
                { data: 'firstname', name: 'firstname', 'class': 'text-center'},
                { data: 'lastname', name: 'lastname', 'class': 'text-center'},
                { data: 'id_no', name: 'id_no', 'class': 'text-center', render: (data,type,row) => data ? data : '-' },
                { data: 'phone', name: 'phone', 'class': 'text-center', render: (data,type,row) => data ? data : '-' },
                { data: 'email', name: 'email', 'class': 'text-center', render: (data,type,row) => data ? data : '-' },
                { data: 'created_at', name: 'created_at', 'class': 'text-center', render: (data,type,row) => ((new Date(data)).toDateString() + ' ' + (new Date(data)).toLocaleTimeString()) },
                { data: 'departure', name: 'departure', 'class': 'text-center', render: (data,type,row) => data ? ((new Date(data)).toDateString() + ' ' + (new Date(data)).toLocaleTimeString()) : '-' },
                { data: null, 'class': 'text-center', render: function(data,type,row) {
                    return `<span>
                      <a onclick="visitorListActionEdit('${data.id}')" class="btn btn-link btn-warning details"><i class="fa fa-edit"></i></a>
                  </span>`
                }}
            ],

            createdRow: function (row, data, index) {
                if(data.departure) {
                    row.classList.add('table-danger');
                }
            }
        };
    }

    edit = (visitor) => {
        this.props.history.push(`${this.props.match.url}/${visitor}`);
    }

	render() {
		let match = this.props.match;
		return <div className="container-fluid">
    	    <div className="row">
        	    <div className="col-md-12">
        	      <span className="pull-right">
        	        <Link to={`${match.url}/new`} className="btn btn-success details">New Visitor</Link>
        	      </span>
        	    </div>
    	    </div>
            <DataTable ref={this.dataTable} title="Today Visitors" config={this.dataTableConfig}>
                <th>Firstname</th>
                <th>Lastname</th>
                <th>ID Number</th>
                <th>Phone Number</th>
                <th>Email</th>
                <th>Arrival</th>
                <th>Departure</th>
                <th className="disabled-sorting text-center noExport">Actions</th>
            </DataTable>
		</div>

	}
}

export class VisitorNew extends Component {
    state = { error: {} };
    submit = (data) => {
        return axios.post('/api/visitors', data).then( res => {
            this.setState({error: {}})
            return res;
        }, error => {
            this.setState({error: error.response.data});
            return Promise.reject(error);
        })
    }

    close = () => {
        this.props.history.push('/school/visitors');
    }
    render() {
        return <VisitorForm error={this.state.error} submit={this.submit} close={this.close} />
    }
}

export class VisitorEdit extends Component {
    state = { error: {}, data: {} };

    componentDidMount() {
    if(this.props.location.state && this.props.location.state.visitor){
        this.setState({data: this.props.location.state.visitor})
    }else if(this.props.match.params.visitorId){
        axios.get(`/api/visitors/${this.props.match.params.visitorId}`).then(response => {
            this.setState({data: response.data});
        },error=>this.setState({error:error.response.data}))
    }
    
  }

  submit = (data) => {
        return axios.put(`/api/visitors/${this.props.match.params.visitorId}`, data).then( res => {
            this.setState({error: {}})
            return res;
        }, error => {
            this.setState({error: error.response.data});
            return Promise.reject(error);
        })
    }

    close = () => {
        this.props.history.push('/school/visitors');
    }

    render() {
        return <VisitorForm edit={true} error={this.state.error} data={this.state.data} submit={this.submit} close={this.close} />
    }
}